/* Navbar */
.navbar {
  display: flex;
  flex-direction: column;
  height: 75px;
}
.navbar .pleca {
  height: 5px;
  width: 100%;
  position: fixed;
  top: 70px;
  z-index: 100;
}

.nav {
  width: 100%;
  height: 70px;
  color: black;
  background-color: white;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 100;
}

.nav div {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.nav .logo {
  height: 70px;
  margin-left: 0px;
}

.nav span {
  margin: 0 20px;
  cursor: pointer;
  font-size: 1rem;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}

.btn-donar {
  color: white;
  background-color: #90b441;
  width: 80px;
  height: 35px;
  margin-right: 30px;
}


.btn-donar span {
  color: white;
  margin: 0 20px;
  cursor: pointer;
  font-size: 1rem;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}

.nav span:hover {
  /* font-weight: bold; */
  color: #E88246;
}

.btn-donar span:hover {
  font-weight: bold;
  color: white;
}

.nav a {
  text-decoration: none;
  color: black;
  /* margin: 0 20px; */
  font-size: 1.5em;
}

.btn-donar a {
  text-decoration: none;
  color: white;
  margin: 0 20px;
  font-size: 1.5em;
}
